/* **************** ARTICLE PERSON ****************** */
.article-person {
  background: #fff;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-person .article-person-content {
  height: 56px;
  padding: 10px;
}
.article-person .article-person-content .article-title {
  color: #696969;
  font-size: 14px;
  line-height: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 8px;
}
.article-person .article-person-content .article-link {
  color: #696969;
}
.article-person .article-person-content .article-posted {
  color: #03b1bc;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.article-person .article-person-action {
  border-top: 0;
  display: flex;
  justify-content: space-between;
  padding: 0;
}
.article-person .person-action {
  background: #00b2b2;
  border-right: 1px solid #6be5e6;
  border-radius: 0;
  padding: 0;
  height: 34px;
  line-height: 34px;
  width: 33.33%;
  transition: background 0.3s ease-in-out;
  outline: none;
}
.article-person .person-action:last-child {
  border-right: 0;
}
.article-person .person-action:focus {
  background-color: #00b2b2;
}
.article-person .person-action:hover {
  background: #009899;
}
.article-person .person-action > * {
  display: inline-block;
  font-size: 12px;
  vertical-align: middle;
}
.article-person .person-action-txt {
  color: #6be5e6;
}
.article-person .person_icon {
  color: #6be5e6;
}
.article-person-img {
  max-height: 160px;
  overflow: hidden;
}
